import {Button, ButtonGroup, Card} from "react-bootstrap";

export default function getCard(imageName, downloadName){
    return (
        <div class="col-md-4">
        <div class="card mb-4 box-shadow">
            <img class="card-img-top" src={`./${imageName}`} alt="Card image cap"></img>
                <ButtonGroup className="d-flex">
                    <Button className="w-100" type="button" variant="dark" href={`./${downloadName}`} download>Download</Button>
                    <Button className="w-100" type="button" variant="dark">Details</Button>
                </ButtonGroup>
        </div>
    </div>
    )
}

export function modelPage(allModels, modelType){
    let postList = [];
    for (let i = 0; i < allModels.length; i++) {
        const model = allModels[i];
        postList.push(getCard(model.image_name, model.file_name));
    }
}  
